.card {
	background-color: white;
	border: 1px solid #333;
	border-radius: 0.5em;
	padding: 0.5em;
	max-width: 300px;
	margin: 0 auto;
	animation: fade 250ms ease forwards;

	&.active {
		animation: slide 250ms 125ms ease both;
	}
}

.hide {
	display: none;
}

@keyframes slide {
	0% {
		transform: translateX(200%);
		opacity: 0;
	}

	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes fade {
	0% {
		transform: scale(1);
		opacity: 1;
	}

	50% {
		transform: scale(0.75);
		opacity: 0;
	}

	100% {
		opacity: 0;
		transform: scale(0);
	}
}

.form-group {
	display: flex;
	flex-direction: column;
	margin-bottom: 0.5rem;
	gap: 0.25em;

	&:last-of-type {
		// margin: 0;
	}

	& > label {
		font-weight: bold;
		font-size: 0.8em;
		color: #333;
	}

	& > input {
		border: 1px solid #333;
		border-radius: 0.25em;
		font-size: 1rem;
		padding: 0.25em;
	}
}

.step-title {
	margin: 0;
	margin-bottom: 1rem;
	text-align: center;
}

.multi-step-form {
	overflow: hidden;
	position: relative;
}
