.card {
  max-width: 300px;
  background-color: #fff;
  border: 1px solid #333;
  border-radius: .5em;
  margin: 0 auto;
  padding: .5em;
  animation: .25s forwards fade;
}

.card.active {
  animation: .25s .125s both slide;
}

.hide {
  display: none;
}

@keyframes slide {
  0% {
    opacity: 0;
    transform: translateX(200%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fade {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  50% {
    opacity: 0;
    transform: scale(.75);
  }

  100% {
    opacity: 0;
    transform: scale(0);
  }
}

.form-group {
  flex-direction: column;
  gap: .25em;
  margin-bottom: .5rem;
  display: flex;
}

.form-group > label {
  color: #333;
  font-size: .8em;
  font-weight: bold;
}

.form-group > input {
  border: 1px solid #333;
  border-radius: .25em;
  padding: .25em;
  font-size: 1rem;
}

.step-title {
  text-align: center;
  margin: 0 0 1rem;
}

.multi-step-form {
  position: relative;
  overflow: hidden;
}

